"use strict"

import * as base from "./base.json";

export default class TypewriterSum {

    // Проверка входных значений на корректность создания экземпляра класса.
    static checkUserSum(currency, user_value) {
        return Number.isFinite(+user_value) &&
            !user_value.includes("e") &&
            Number(user_value) <= 7.5e12 &&
            Object.keys(base.CURRENCY).includes(currency);
    }

    constructor(currency, first, second = "00",) {
        this.first = (first) ? first : "0";
        this.second = second.slice(0, 2).padEnd(2, "0");
        this.cur_first = this.writeCur(this.first, base.CURRENCY[currency][0]);
        this.cur_second = this.writeCur(this.second, base.CURRENCY[currency][1]);
    }

    gender = (digit, order) => (order === 2) ?
            base.UNITS_1_2.female[digit] : base.UNITS_1_2.male[digit]

    writeCur(digit, currency) {
        return currency[(digit.slice(-2, -1) === "1") ? 2 :
            (digit.slice(-1) === "1") ? 0 :
                ("234".includes(digit.slice(-1))) ? 1 : 2];
    }

    writeOrder() {
        const write_order_from_cur = this.writeCur;
        const orders = base.ORDERS;
        return {
            [Symbol.iterator]() {
                let step = -2;
                const len = base.ORDERS.length - 1;
                return {
                    next: (digit) => {
                        step += 1;
                        return {
                            value: (step !== -1 && +digit) ?
                                (step <= len) ? write_order_from_cur(digit, orders[step]) :
                                    "(Пропишите порядок!)" : undefined, done: true
                        }
                    }
                }
            }
        }
    }

    distributeFirst(i = 1) {
        let o = this.first.substring(this.first.length - 3 * i,
            this.first.length - (i - 1) * 3);
        if (o === "") return "";
        return `${this.distributeFirst(++i)} ${o}`.trimStart();
    }

    [Symbol.iterator]() {
        const ordersIt = this.writeOrder()[Symbol.iterator]();
        const L = Math.ceil(this.first.length / 3);
        let o = 0;
        return {
            next: () => {
                o += 1;
                return {
                    value: [
                        base.HUNDREDS[this.first.charAt(this.first.length - 3 * o)],
                        base.DECADES[this.first.charAt(this.first.length - (3 * o - 1))],
                        base.DECADES_10[this.first.substring(this.first.length - (3 * o - 1),
                            this.first.length - (o - 1) * 3)] ||
                        base.UNITS_all[this.first.charAt(this.first.length - (3 * o - 2))] ||
                        this.gender(this.first.charAt(this.first.length - (3 * o - 2)), o),
                        ordersIt.next(this.first.substring(this.first.length - 3 * o,
                            this.first.length - (o - 1) * 3)).value
                    ].filter((x) => x).join(" "),
                    done: o > L
                }
            }
        }
    }

    [Symbol.toPrimitive](hint) {
        if (hint === "string") {
            if (!+this.first) {
                return base.NULL;
            }
            return [...this].filter((x) => x).reverse().join(" ").replace(/\S/, (l) => l.toUpperCase());
        }
    }
}