// Блок кода с реализацией копирования в буфер результата суммы договора.
const writeCb1 = document.getElementById('writeClipboard1');
writeCb1.addEventListener("click", event => {
    event.preventDefault();
    navigator.clipboard.writeText(window.JSON.parse(window.sessionStorage.getItem("storage1"))[3])
        .then(() => {
            // Получилось!
        })
        .catch(err => {
            console.log('Something went wrong', err);
        });

});

