// Реализация горячих клавиш.
document.addEventListener('keydown', function (event) {
    if (event.code == 'KeyQ' && (event.ctrlKey || event.metaKey)) {
        navigator.clipboard.writeText(window.JSON.parse(window.sessionStorage.getItem("storage1"))[3]).then(() => {
        })
    }

    if (event.code == 'KeyX' && (event.ctrlKey || event.metaKey)) {
        navigator.clipboard.writeText(window.JSON.parse(window.sessionStorage.getItem("storage1"))[4]).then(() => {
        })
    }

    if (event.code == 'KeyB' && (event.ctrlKey || event.metaKey)) {
        navigator.clipboard.writeText(window.JSON.parse(window.sessionStorage.getItem("storage2"))[1]).then(() => {
        })
    }
})